.Champion-Detail-Container {
    margin: 8vh 10vh;
    display: flex;
    justify-content: center;
    text-align: center;
    min-height: 65vh;
    /* box-shadow: 0px 0px 70px 5px rgba(0,0,0,0.18); */
}

/* Disable site effects of using Link in champion card */
.Champion-Detail-Left,
.Champion-Detail-Right {
    color: #dbd8d8;
    box-shadow: 1px 0px 2px -1px #dbd8d8;
}
.Champion-Detail-Left .Champion-Card { 
    color: #dbd8d8;
    font-weight: 700;
    margin: 1vh auto; 
    padding: 1vh 0; 
}
.Champion-Detail-Left .Champion-Card:hover { 
    text-decoration: none; 
    width: max-content; 
    color: unset;
}
.Champion-Detail-Left {
    width: 40%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.Champion-Detail-Left img {
    border-radius: 0.5vh;
}
.Champion-Detail-Left-Up {
    box-shadow: 0px 1px 1px -1px #dbd8d8;
    padding: 2vh 0;
}
.Champion-Detail-Left-Up-Stats p { margin: 0;}
.Champion-Detail-Left-Middle {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2vh auto;
}
.Champion-Detail-Left-Middle img {
    max-height: 5vh;
    margin: 0 1vh;
}
.Champion-Detail-Left-Down .Champion-Ability {
    margin: 0;
    padding: 2vh 1vh 1vh;
    box-shadow:0px -1px 1px -1px #dbd8d8;
}


.Champion-Detail-Right {
    width: 60%;
}
.Champion-Detail-Right-Up img {
    max-height: 3vh;
}
.Champion-Detail-Right-Up .Trait-Description,
.Champion-Ability { 
    display: flex;
    align-items: center;
    margin: 2vh;
}
.Champion-Detail-Right-Up .Trait-Description,
.Champion-Ability p { 
    color: #dbd8d8; text-align: left;
}

.Champion-Detail-Right-Down { 
    padding: 2vh;
}
.Champion-Detail-Right-Down h5 {
    width: 100%;
    text-align: left;
    margin: 2vh 0;
}
.Champion-Detail-Right-Down-Synergy {
    display: flex;
    align-items: center;
    padding-top: 1vh;
    box-shadow: 0px -1px 1px -1px #dbd8d8;
}
.Champion-Detail-Right-Down-Synergy-Trait-Image {
    width: 10%;
}
.Champion-Detail-Right-Down-Synergy-Champion-Card-Container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.Champion-Detail-Right-Down .Champion-Card { 
    padding: 0vh;
    margin: 1vh 2vh;
    min-width: 100px;
}

.Champion-Detail-Right-Down img { max-height: 6vh; }
/* .Champion-Detail-Right-Down p { margin: 0; } */


@media screen and (max-width: 1400px) { 
    .Champion-Detail-Container { 
        margin: 5vh; 
    }
    .Champion-Detail-Container .Champion-Card:nth-child(4n) .Tool-Tip {
        left: -30%;
    }
}
@media screen and (max-width: 1100px) { 
    .Champion-Detail-Container .Champion-Card:nth-child(3n) .Tool-Tip {
        left: -50%;
    }
}

@media screen and (max-width: 1000px) { 
    .Champion-Detail-Container { 
        display: grid;
        grid-template-columns: 1fr;
        margin: 5vh 1vh;
    } 
    .Champion-Detail-Left {
        padding: 2vh 0vh;
        width: 100%;
    }
    .Champion-Detail-Left,
    .Champion-Detail-Right {
        box-shadow: 0px 1px 2px -1px #dbd8d8;
    }
    .Champion-Detail-Left-Middle .Item-Card:nth-child(2) .Tool-Tip {
        left: 50%;
    }
    .Champion-Detail-Left-Middle .Item-Card:nth-child(2) .Tool-Tip {
        left: -75%;
    }
    .Champion-Detail-Left-Middle .Item-Card:nth-child(3) .Tool-Tip {
        left: -125%;
    }
    .Champion-Detail-Right {
        width: 100%;
    }
    .Champion-Detail-Right img {
        max-height: 7vh;
    }
    .Champion-Detail-Right-Down .Champion-Card { 
        margin: 0.5vh auto;
    }
    .Champion-Detail-Right-Down-Synergy-Champion-Card-Container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
    .Champion-Detail-Container .Champion-Card .Tool-Tip {
        left: -35%;
    }
    .Champion-Detail-Container .Champion-Card:nth-child(3n) .Tool-Tip {
        left: -100%;
    }
}

